.App {
  font-family: sans-serif;
  text-align: center;
}

.header {
  background-color: black;
  min-height: 10vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  padding-bottom: 10px;
}

.box {
  margin: 20px;
  width: 30%;
  border-image-width: flex;
  align-items: center;
}

.img {
  max-width: 35%;
}
.grid {
  min-height: 100%;
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
}

.cards {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
